import React, { useState, useEffect } from "react";
import {
    Alert,
    Modal,
    StyleSheet,
    Text,
    Pressable,
    View,
    Button,
    FlatList,
    Platform,
    TouchableOpacity,
    SafeAreaView
} from "react-native";
import {MaterialIcons} from "@expo/vector-icons";
import {Select} from "../../../templateComponents/Select/Select";
import {useDispatch, useSelector} from "react-redux";
import {setCallOperator} from "../../../store/actions/post";

export const CallOperator = ()=>{
    const dispatch = useDispatch();


    const SelectOption = ()=>{
        const callOperator = useSelector(state=>state.post.callOperator);

        const handleChangeCallOperator = (type)=>{
            dispatch(setCallOperator(type));
        };


        let ItemsType = [];

            ItemsType = [
                {title:"Не нужно перезванивать",value:1},
                {title:"Требуется звонок оператора, для подтверждения заказа",value:2},
            ]

        console.log(callOperator);
        return (<Select
            defaultValue={callOperator}
            value={callOperator}
            fontSize={12}
            item={ItemsType}
            keys="CallOperator"
            onChange={handleChangeCallOperator}
        />)
    };


    const CallOperator =
        <View
            style={{flexDirection:"row",justifyContent:"space-between",alignItems:"center",width:"100%"}}
            onPress={
                ()=>{}
            }
        >
            <View style={{flexDirection:"row",justifyContent: 'flex-start',alignItems:"center",width:"95%"}}>
                {SelectOption()}
            </View>
            <View style={styles.arrowRight}><MaterialIcons name="keyboard-arrow-right" size={20} color="black" /></View>
        </View>
    ;

    return (
        <View style={[styles.BoxWhiteBlock,styles.shadow]}>
            {CallOperator}
        </View>
    )
};

const styles = StyleSheet.create({
    BoxWhiteBlock:{
        backgroundColor:"#ffffff",
        borderRadius:10,
        paddingHorizontal:10,
        paddingVertical:10,
    },
    AdressStr:{
        width:"100%",
        flexDirection:'row',
        justifyContent: 'space-between',
        alignItems: 'center',
        paddingHorizontal:2,
        paddingVertical:10,
        marginVertical:0,
        borderColor:"#d6d6d6",
        borderTopWidth:0,
        // backgroundColor: "#ff7300"
    },
    textIconCenter:{
        flexDirection:'row',
        // width:"100%",
        maxWidth:"84%",
        marginLeft:5,
        alignItems:'center',
        justifyContent:'space-between',
        fontSize:12,
        textAlign:"left",
        // backgroundColor:"#ff7300"
    },
    arrowRight:{
        width:"5%",
        marginLeft:0,
        paddingTop:Platform.OS === 'ios' ? 0 : 2
    },
    shadow:{
        borderWidth:0,
        shadowColor: "#000",
        shadowOffset: {
            width: 0,
            height: 2
        },
        shadowOpacity: 0.22,
        shadowRadius: 40,
        elevation: 3,
        marginBottom:0
    },
});